/* Title, Subtitle */

.title {
  text-align: center;
  position: relative;

  margin-bottom: 32px;

  &:after {
    content: '';
    display: block;
    height: 3px;
    width: 70px;
    background: rgba($orange, 0.7);
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    margin-top: 15px;
  }
}
.subtitle {
    margin: 0 auto;
    width: 85%;
    text-align: center;
}