/* SIDEBAR */

.sidebar {
  width: 150px;
  height: 100vh;
  background-color: $bg-light;
  border-right: 1px solid $border;
  box-shadow: 0 0 5px $border;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  font-weight: 600;
  position: relative;
  text-align: center;
  z-index: 1000;
}

/* Photo */
.photo {
  margin: 10px auto 20px;
  width: 130px;
  height: 145px;
  border-radius: 50%;
  background-color: $orange;
  object-fit: cover;
}

/* Name */
.name {
  margin-bottom: 15px;
}

/* Position */
.position {
  font-size: 14px;
}

/* Navigation */
.nav {
  &__item {
    margin-left: 0;
    margin-bottom: 0;
    display: block;
    font-family: inherit;

  }
  &__btn {
    width: 100%;
    font-family: 'Inter', serif;
    font-size: 16px;
    background-color: transparent;
    border: none;
    color: inherit;
    padding: 10px;
    transition: all .2s ease-in;

    &:hover {
      background-color: $orange;
    }
  }
}

/* Contants */
.contacts {
  margin: auto auto 30px;

  &__item {
    display: block;
    width: 50px;
    border: 1px solid $border;
  }
  &__link, &__btn {
    width: 100%;
    height: 100%;
    padding: 10px;
  }

  &__btn {
    background-color: transparent;
    border: none;
    outline: none;
  }

  &__content {
    position: absolute;
    left: 150px;
    bottom: 0;
    background-color: $bg-light;
    padding: 15px;
    display: flex;
    width: 600px;
    font-size: 18px;
    transform: translateY(150%);
    transition: transform .4s ease-in;
    z-index: 50;

    a {
      text-decoration: none;
      color: inherit;
      margin: 0 20px;
    }
  }
  .active {
     transform: translateY(0%);
    }

}

.icon {
  width: 22px;
  height: 22px;
  fill: $color;
  vertical-align: middle;
}