body {
  width: 100%;
  margin: 0;
  font-size: 16px;
  background-color: $bg-dark;
  color: $color;
  font-family: "Inter", serif;
  line-height: 1.4;
  font-weight: 400;
}

*, *:before, *:after {
  box-sizing: border-box;
}
ul {
  margin: 0;
  padding: 0;
}
li {
  font-size: inherit;
  line-height: 1.4;
  margin-bottom: 3px;
}

h1 {
  font-size: 26px;
  line-height: 1.2;
}

h2 {
  font-size: 28px;
  line-height: 1.2;
}
h3 {
  font-size: 20px;
  line-height: 1.5;
  margin: 15px 0 10px;
}

button {
  color: inherit;
  cursor: pointer;
}

a {
  display: block;
}

.page {
  width: 100%;
  display: flex;
}