/* PROJECT */
.projects {
  display: flex;
  flex-direction: column;
}

.project {
  margin-top: 80px;
  position: relative;

  &:last-child {
    margin-bottom: 80px;
  }

  &__img {
    width: 40%;
    object-fit: cover;
  }
  &__content {
    display: flex;
    flex-direction: column;
    margin-top: 20px;
  }
  &__header {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    margin-bottom: 10px;
  }
  &__title {
    text-transform: uppercase;
  }
  &__subtitle {
    color: gray;
    font-size: 18px;
  }
  &__desc {
    font-size: 16px;
    margin-top: 0;
  }
  &__links {
    display: flex;
    margin-right: 15px;
  }
  &__link {
    border: 2px solid rgba($orange, 0.7);
    background-color: transparent;
    color: rgba($orange, 0.7);
    padding: 10px 15px;
    text-transform: uppercase;
    border-radius: 3px;
    transition: all .2s ease-in;
    text-decoration: none;


    &:hover {
      border-color: $orange;
      background-color: $orange;
      color: $bg-dark;
      cursor: pointer;;
    }
  }
  &__link + &__link {
    margin-left: 10px;
  }
  &__list {
    margin-left: 15px;
  }
}

.list {

  &--plans li {
    list-style: circle;
  }
  &--completed li {
    list-style: disc;
  }
}

.list {
  display: flex;

  &__item {
    display: block;
    min-width: 50px;
    padding: 5px 10px;
    background-color: $border;
    border: 1px solid $border;
    border-radius: 3px;
    text-align: center;
    text-transform: uppercase;
  }
  &__item + &__item {
    margin-left: 10px;
  }
  &--second {
    margin-top: 10px;
  }
}

